<template>
  <div>
    <div class="cms-list-layout">
      <cms-prompt>
        <li> 关联版式可分离商品详情介绍中公共部位，方便商家针对商品详情信息进行批量更新</li>
      </cms-prompt>
      <section class="cms-list-query">
        <div class="query-group">
          <label>版式名称</label>
          <el-input v-model="queryParams.name" @change="query"></el-input>
        </div>
        <div class="query-group">
          <label>位置</label>
          <el-select v-model="queryParams.position" @change="query">
            <el-option label="顶部" :value="1"></el-option>
            <el-option label="底部" :value="2"></el-option>
          </el-select>
        </div>
        <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
        </div>
      </section>
        <section class="cms-batch">
          <cms-button icon="add" type="success" size="small" @click="$routerLink('/product/format/save','save')">新增</cms-button>
          <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
        </section>
      <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
        <el-table-column type="selection" width="46" fixed='left'></el-table-column>
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="name" label="名称"></el-table-column>
        <el-table-column prop="useScene" label="位置">
          <template slot-scope="scope">
            <div class="text-overflow">
              <span v-if="scope.row.position==1">顶部</span>
              <span v-if="scope.row.position==2">底部</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width='175' fixed="right" align="center">
          <div slot-scope="scope">
            <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/product/format/update','update',{id:scope.row.id})">编辑</cms-button>
            <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
    </div>
  </div>
</template>

<script>
import list from '@/mixins/list'
import {
  productFormatPage,
  productFormatDelete
} from './../../../../api/product/product.js'
var defaultData = {
  size: 15,
  page: 1,
  position: '',
  name: ''
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      typeList: []
    }
  },
  computed: {},
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(productFormatDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(productFormatDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  created() {
    this.getTableData(productFormatPage, this.queryParams)
  }
}
</script>

<style>
</style>



